@import url('https://fonts.googleapis.com/css?family=Open+Sans&display=swap');

.App {
  font-family: sans-serif;
  text-align: center;
}

#root {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

.credits {
  position: fixed;
  bottom: 30px;
  left: 30px;
  z-index: 10;
  color: white;
  font-family: 'Noto Sans JP', sans-serif;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: -0.02em;
  opacity: 0.5;
  transition: all 0.6s;
}
